import React, { useState } from "react";
import "../css/sidebar.css";
import Swal from 'sweetalert2'
import { Link } from "react-router-dom";
import { useNavigate } from 'react-router-dom';
import Home from "Home";

const Sidebar = ({ selectedItem, onItemClick }) =>{
    const navigate = useNavigate();

    const Logout = () =>{


        Swal.fire({
            title: ' ?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            }).then((res) => {
                if (res.isConfirmed) {
                Swal.fire({
                    title: " .",
                    icon: "info"
                });
                //   
                sessionStorage.clear();
                navigate('/');
            }
            else{
                return;
            }
            })

    }




    return (
        <div className="menu">
            <br /><br />
            <Link to="/home">
                <h1 onClick={Home}>intelly cosm</h1>
            </Link>
            <br />
            <button className={selectedItem === "" ? "selected" : "unselected"} onClick={() => onItemClick("")}>
                
            </button>
            <button className={selectedItem === " " ? "selected" : "unselected"} onClick={() => onItemClick(" ")}>
                 
            </button>
            <button className={selectedItem === " " ? "selected" : "unselected"} onClick={() => onItemClick(" ")}>
                 
            </button>
            <button className={selectedItem === "() " ? "selected" : "unselected"} onClick={() => onItemClick("() ")}>
                () 
            </button>
            <button className={selectedItem === " " ? "selected" : "unselected"} onClick={() => onItemClick(" ")}>
                 
            </button>
            <button className={selectedItem === "  " ? "selected" : "unselected"} onClick={() => onItemClick("  ")}>
                  
            </button>
            <button className={selectedItem === " " ? "selected" : "unselected"} onClick={() => onItemClick(" ")}>
                 
            </button>
            <button className={selectedItem === "AI " ? "selected" : "unselected"} onClick={() => onItemClick("AI ")}>
                AI 
            </button>
            <div className="logout" onClick={Logout}>
                <button></button>
            </div>
        </div>
    );
}

export default Sidebar;
