import React, { useState } from "react";
import '../../css/QandA.css'
import Swal from "sweetalert2";

const QandAAnswer = ({ rowData, goBack, buttonLabel, setIsEditing }) => {
    const [data, setData] = useState(rowData);

    const handleEditClick = (e) => {


      if(buttonLabel === ''){
        //= setIsEditing(true);
        //   
        console.log(data);
        

            Swal.fire({
              title: '  ?',
              icon: 'warning',
              showCancelButton: true,
              confirmButtonText: '',
              cancelButtonText: '',
              confirmButtonColor: "#3085d6",
              cancelButtonColor: "#d33",
              }).then((res) => {
                if (res.isConfirmed) {
                  fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/qna/answer2/${data.id}`, {
                  method: 'PUT',
                  body: JSON.stringify(data),
                  headers: {
                      'Content-Type': 'application/json'
                  },
            })
              .then(response => {
                if (response.ok) {
                  Swal.fire({
                    title: "  !",
                    icon: "success"
                  });
                  goBack();
                  console.log('');
                } else {
                  throw new Error('   .');
                }
              })
              .catch(error => {
                console.error('Error:', error);
              });
                    }
              });
          }
          else{
            Swal.fire({
              title: '  ?',
              icon: 'warning',
              showCancelButton: true,
              confirmButtonText: '',
              cancelButtonText: '',
              confirmButtonColor: "#3085d6",
              cancelButtonColor: "#d33",
              }).then((res) => {
                if (res.isConfirmed) {
                  fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/qna/answer/${data.id}`, {
                  method: 'PUT',
                  body: JSON.stringify(data),
                  headers: {
                      'Content-Type': 'application/json'
                  },
            })
              .then(response => {
                if (response.ok) {
                  Swal.fire({
                    title: "  !",
                    icon: "success"
                  });
                  goBack();
                  console.log('');
                } else {
                  throw new Error('   .');
                }
              })
              .catch(error => {
                console.error('Error:', error);
              });
                    }
              });
          }
}
   
    const handleAnswerChange = (e) => {
        setData(prevData => ({
            ...prevData,
            answer: e.target.value
        }));
    }

    //    
  const handleDeleteClick = () => {
    Swal.fire({
      title: '  ?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      }).then((res) => {
        if (res.isConfirmed) {
          fetch('http://ceprj.gachon.ac.kr:60006/api/admin/qna/delete/' + data.id, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => {
        if (response.ok) {
          Swal.fire({
            title: " !",
            icon: "success"
          });
          goBack();
          console.log('');
        } else {
          throw new Error('   .');
        }
      })
      .catch(error => {
        console.error('Error:', error);
      });
            }
      });
  };

    return (
        <div>
        <div className='infotext'> </div>
        <div className='QandAContainer'>
            <div className="QandAContentContainer">
                <span className="userIDText"> ID : {data.member.login_id}</span>
                <div className="qnaBox2">
                    <span style={{ fontSize: "24px" }}> </span>
                    <div className="answerTextField"><span>{data.description}</span></div>
                </div>
            </div>
            <div className="qnaBox">
                <span style={{ marginTop: "100px", fontSize: "24px" }}></span>
                <div className="answerBox1">
                    <textarea
                        className="answerTextField"
                        value={data.answer}
                        onChange={handleAnswerChange} />
                </div>
                <div className="answerBt">
                    <button onClick={handleEditClick} className="history">{buttonLabel}</button>
                    {buttonLabel === '' ? (<button onClick={handleDeleteClick} style={{marginLeft:'24px'}}  className="history"></button>) : (<></>)}
                    <button style={{marginLeft:'24px'}} onClick={goBack} className="history"></button>
                </div>
            </div>
        </div>
        </div>
    );
};

export default QandAAnswer;