import React, { useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2'
import "../css/dashboard.css";
import Sidebar from "../../sidebar/component/sidebar";
import SideBarHome from "../../sidebar/page/home/home";
import ManageUser from "../../sidebar/page/user_manage/manage_user";
import RegisterCosmetic from "../../sidebar/page/cosmetic_register/register_cosmetic";
import CosmeticLink from "../../sidebar/page/cosmetic_link/cosmetic_link";
import Evaluation from "../../sidebar/page/evaluation/evaluation";
import Ingredient from "../../sidebar/page/ingredient/ingredient";
import QandA from "../../sidebar/page/QandA/QandA";
import Manage_AI from "../../sidebar/page/AI/manage_AI";

//    import

export default function Dashboard() {

  const navigate = useNavigate();

  useEffect(() => {
    //  ,   .
    const sessionExists = isLoggedIn(); //    

    if (!sessionExists) {
      Swal.fire({
        title: " .",
        icon: "error"
      });
      navigate('/login'); //     
    }
  }, [navigate]);

  const isLoggedIn = () => {
    //     .
    const session = JSON.parse(sessionStorage.getItem('session'));
    
  
    //    true ,  false .
    return session ? true : false;
  };

  const [selectedItem, setSelectedItem] = useState("");

  const handleItemClick = (item) => {
    setSelectedItem(item);
  };

  const renderSelectedPage = () => {
    switch (selectedItem) {
      case "":
        return <SideBarHome />;
      case " ":
        return <ManageUser handleItemClick={handleItemClick} />;
      case " ":
        return <RegisterCosmetic handleItemClick={handleItemClick} />;
      case "() ":
        return <CosmeticLink handleItemClick={handleItemClick} />;
      case " ":
        return <Evaluation handleItemClick={handleItemClick} />;
      case "  ":
        return <Ingredient handleItemClick={handleItemClick} />;
      case " ":
        return <QandA handleItemClick={handleItemClick} />;
      case "AI ":
        return <Manage_AI handleItemClick={handleItemClick} />;
      default:
        return null;
    }
  };

  return (
    <div className="dashboard-container">
      <div className="sidebar">
        <Sidebar selectedItem={selectedItem} onItemClick={handleItemClick} />
      </div>
      <div className="container">
        {renderSelectedPage()}
      </div>
    </div>
  );
}
