import React, { useState } from "react";
import LinkRow from "../../component/cosmetic_link/link_row";
import Swal from "sweetalert2";

const CosmeticInfo = ({ rowData, goBack }) => {
    const [data, setData] = useState(rowData);
    const [purchaseLinks, setPurchaseLinks] = useState(data.cosmeticPurchaseLinks);

    const handleNameChange = (e) => {
        console.log(data);
        setData(prevData => ({
            ...prevData,
            cosmeticPurchaseLinks: e.target.value
        }));

    };

    //   
    const handleLinkChange = (index, fieldName, newValue) => {
        const newPurchaseLinks = [...purchaseLinks];
        newPurchaseLinks[index][fieldName] = newValue;
        setPurchaseLinks(newPurchaseLinks);
    };

    const handleRevise = () => {
        Swal.fire({
            title: ' ?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
        }).then((res) => {
            if (res.isConfirmed) {
                fetch('http://ceprj.gachon.ac.kr:60006/api/admin/link/update/' + data.id, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(purchaseLinks),
                })
                    .then(response => {
                        if (response.ok) {
                            Swal.fire({
                                title: " !",
                                icon: "success"
                              });
                              goBack();
                        } else {
                            Swal.fire({
                                title: "  !",
                                icon: "success"
                              });
                              goBack();
                            throw new Error('   .');
                        }
                    })
                    .catch(error => {
                        console.error('Error:', error);
                    });
            }
        });
    }

    const handleDelete = () => {
        Swal.fire({
            title: '  ?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            }).then((res) => {
              if (res.isConfirmed) {
                fetch('http://ceprj.gachon.ac.kr:60006/api/admin/cosmetic/delete/' + data.id, {
            method: 'DELETE',
            headers: {
              'Content-Type': 'application/json',
            },
          })
            .then(response => {
              if (response.ok) {
                Swal.fire({
                  title: " !",
                  icon: "success"
                });
                goBack();
                console.log('');
              } else {
                throw new Error(' .');
              }
            })
            .catch(error => {
              console.error('Error:', error);
            });
                  }
            });
    }


    return (
        <div className="LLLL">
        <div className="total-link-container">
         
            <div className="link-container">
                <img className='infoImage' src={data.image_path} alt="" />
                <div className="Link_name">{data.name}</div>
                <div className="Link_Date">   : {data.latest_update_date}</div>
            </div>
            <div className="Link_Input_Box">
                
                <div >
                    {data.cosmeticPurchaseLinks.map((purchaseLink, index) => (
                        <div className="purchase-container" key={index}>
                            <input
                                className="purchaseSite"
                                type="text"
                                value={purchaseLink.purchaseSite}/>
                            <div>
                                <LinkRow label="" value={purchaseLink.price} onChange={(newValue) => handleLinkChange(index, 'price', newValue)}/>
                                <LinkRow label="" value={purchaseLink.url} onChange={(newValue) => handleLinkChange(index, 'url', newValue)} />
                            </div>
                        </div>
                    ))}
                </div>
                <div className="link_bts_box">
                    <button onClick={handleRevise} className='bt' ></button>
                    <button onClick={handleDelete} className='bt' ></button>
                    <button onClick={goBack} className='bt' ></button>
                </div>
            </div>
            </div>
        </div>
    );
};

export default CosmeticInfo;