import React, {useState} from 'react';
import UserInfoField from './user_info_row';
import '../../css/user_manage.css'
import UserRadio from './user_radio';
import SkinConcernButtons from './skin_concern';
import Swal from 'sweetalert2'

const UserInfo = ({rowData, handleIsEditingChange,  status, setStatus }) => {
  console.log(rowData);
    const [data, setData] = useState(
        rowData
      );

    const genderData = [
        { name: "" },
        { name: "" },
    ];

    const skinTypeData = [
        { name: "" },
        { name: "" },
        { name: "" },
    ];

    const skinConcernData = [
        { name: "" },
        { name: "" },
        { name: "" },
        { name: "" },
        { name: "" },
        { name: "" },
    ];

    const goBack = () => {
      handleIsEditingChange();
    }
    
    const handleNicknameChange = (newNickname) => {
        setData(prevData => ({
          ...prevData,
          nickname: newNickname
        }));
    };

    const handleIdChange = (newId) => {
        setData(prevData => ({
          ...prevData,
          id: newId
        }));
    };

    const handlePwChange = (newPw) => {
        setData(prevData => ({
          ...prevData,
          password: newPw
        }));
    };

    const handleNameChange = (newName) => {
        setData(prevData => ({
          ...prevData,
          name: newName
        }));
    };

    const handleEmailChange = (newEmail) => {
        setData(prevData => ({
          ...prevData,
          email: newEmail
        }));
    };

    const handleBirthChange = (newBirth) => {
        setData(prevData => ({
          ...prevData,
          birth: newBirth
        }));
    };

    const handleAllergieChange = (newAllergie) => {
        setData(prevData => ({
          ...prevData,
          allergy: newAllergie
        }));
    };

    const handleGenderChange = (newGender) => {
        setData(prevData => ({
          ...prevData,
          gender: newGender === '' ? 'MALE' : 'FEMALE'
        }));
    };

    const handleSkinTypeChange = (newSkinType) => {
        setData(prevData => ({
          ...prevData,
          skin_type: newSkinType === '' ? 'DRY' : newSkinType === '' ? 'OILY' : newSkinType === '' ? 'SENSITIVE' : ''
        }));
    };

    const handleSkinConcernChange = (value) => {
      if(value === ''){
        setData(prevData => ({
          ...prevData,
          skin_concern: ['']
        }));
      }
      else{
        setData(prevData => ({
          ...prevData,
          skin_concern: [...prevData.skin_concern, value].filter(item => item !== '')
        }));
      }
    };
    

    const handleUpdateUserInfo = () => {
      fetch('http://ceprj.gachon.ac.kr:60006/api/admin/memeber/udate/' + data.login_id, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      })
        .then(response => {
          if (response.ok) {
            Swal.fire({
              title: " .",
              icon: "success"
            })
            setStatus(1);
            console.log('');
            goBack();
          } else {
            Swal.fire({
              title: " .",
              icon: "success"
            })
            throw new Error('   .');
          }
        })
        .catch(error => {
          console.error('Error:', error);
        });
    };

    const handleDeleteUserInfo = () => {



      fetch('http://ceprj.gachon.ac.kr:60006/api/admin/member/delete/' + data.login_id, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
      })
        .then(response => {
          if (response.ok) {
            console.log('');
          } else {
            throw new Error('   .');
          }
        })
        .catch(error => {
          console.error('Error:', error);
        });


            Swal.fire({
              title: ' ?',
              icon: 'warning',
              showCancelButton: true,
              confirmButtonText: '',
              cancelButtonText: '',
          }).then((res) => {
            if (res.isConfirmed) {
     

              fetch('http://ceprj.gachon.ac.kr:60006/api/admin/member/delete/' + data.login_id, {
                method: 'DELETE',
                headers: {
                  'Content-Type': 'application/json',
                },
              })
                .then(response => {
                  if (response.ok) {
                    Swal.fire({
                      title: " !",
                      icon: "success"
                    });
                    setStatus(1);
                    goBack();

                  } else {
                    Swal.fire({
                      title: " !",
                      icon: "success"
                    });
                    setStatus(1);
                    goBack();
                  }
                })
                .catch(error => {
                  console.error('Error:', error);
                });





            }
          });





      
    };
  return (
    
    <div>
        <div className='infotext'>  </div>
    <div className='user-info-container'>
      <div className='user-info-container-in'>
        <div className='userInputBox'>
        <UserInfoField label="" value={data.nickname} onChange={handleNicknameChange} />
          <UserInfoField label="" value={data.login_id} onChange={handleIdChange} />
          <UserInfoField label="" value={data.password} onChange={handlePwChange} />
          <UserInfoField label="" value={data.name} onChange={handleNameChange} />
          <UserInfoField label="" value={data.email} onChange={handleEmailChange} />
          <UserInfoField label="" value={data.birth} onChange={handleBirthChange} />
          <UserInfoField label="" value={data.allergy} onChange={handleAllergieChange} />
        </div>
        <div className='line'>
          <UserRadio text='' data={genderData} selectedValue={data.gender === 'MALE' ? '' : ''} handleChange={handleGenderChange}/>
          <UserRadio text='' data={skinTypeData} selectedValue={data.skin_type === 'DRY' ? '' : data.skin_type === 'OILY' ?  '' : data.skin_type === 'SENSITIVE' ? '' : ''} handleChange={handleSkinTypeChange}/>
          <br />
          <SkinConcernButtons  text='' data={skinConcernData} selectedValue={data.skin_concern.length == 0 ? '' : data.skin_concern} handleChange={handleSkinConcernChange}/>
          </div>
          <div className='infobuttons'>
          <button className='bt' onClick={handleUpdateUserInfo}></button>
          <button className='bt' onClick={handleDeleteUserInfo}></button>
          <button className='bt' onClick={goBack} ></button>
        
      </div>
      </div>
    </div>
    </div>
  );
};

export default UserInfo;