import React, {useState, useEffect} from "react"
import '../../css/home.css'
import NumConatiner from "../../component/home/num_conatiner";
import { PieGraph } from "../../component/home/pie";
import HorizontalBarChart from "../../component/home/graph";
import LineChart from "../../component/home/chart";
import AreaChart from "../../component/home/area";

//npm i react-chartjs-2 chart.js   
const SideBarHome = () => {
    const [data, setData] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
        try {
            //    API 
            const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/dashboard/1');
            const fetchedData = await response.json();
            setData(fetchedData);
            
            



            
        } catch (error) {
            console.error('Error fetching data:', error);
        }
        };

        fetchData();
    }, []);

    const lineData = data.length == 0 ? [] : data.user_activities_status

    const areaData = data.length == 0 ? [] : data.analysis_activities_status

    const evalData = [
        { "label": "5", "value": data.length == 0 ? '' : data.analysis_service_evaluation_status.fiveRate, "color" : "#03D966" },
        { "label": "4", "value": data.length == 0 ? '' : data.analysis_service_evaluation_status.fourRate, "color" : "#03D966"},
        { "label": "3", "value": data.length == 0 ? '' : data.analysis_service_evaluation_status.threeRate, "color" : "#FE9B27"},
        { "label": "2", "value": data.length == 0 ? '' : data.analysis_service_evaluation_status.twoRate, "color" : "#FF0000"},
        { "label": "1", "value": data.length == 0 ? '' : data.analysis_service_evaluation_status.oneRate,"color" : "#FF0000" },
    ]

    const evalData2 = [
        { "label": "5", "value": data.length == 0 ? '' : data.recommendation_evaluation_status.fiveRate, "color" : "#03D966" },
        { "label": "4", "value": data.length == 0 ? '' : data.recommendation_evaluation_status.fourRate, "color" : "#03D966"},
        { "label": "3", "value": data.length == 0 ? '' : data.recommendation_evaluation_status.threeRate, "color" : "#FE9B27"},
        { "label": "2", "value": data.length == 0 ? '' : data.recommendation_evaluation_status.twoRate, "color" : "#FF0000"},
        { "label": "1", "value": data.length == 0 ? '' : data.recommendation_evaluation_status.oneRate,"color" : "#FF0000" },
    ]
    
    return (<div>
        <div className="part1">
        <div className="title">DashBoard</div>
        <div className="title2">Wellcome to Admin</div><hr className="n1"/><br /><br /><br />
        <div className="parentNumC">
            <div className="numContainer">
                <NumConatiner text={"  "} num={data.number_of_real_time_users} image={"images/image 560.svg"} />
                <NumConatiner text={"  "} num={data.number_of_users_today} image={"images/image 561.svg"}/>
                <NumConatiner text={"   "} num={data.number_of_use_analysis_service} image={"images/image 563.svg"}/>
            </div><br />
            <div className="numContainer">
                <NumConatiner text={" "} num={data.number_of_unanswered_questions} image={"images/image 562.svg"}/>
                <NumConatiner text={"  "} num={data.number_of_Cosmetics_can_registered} image={"images/image 564.svg"}/>
            </div>
        </div>
        <br /><br /><br /><br />
        </div>
        <div className="part2">
            <div className="chartgraph">
                <div className="chart">
                <br /><div className="name">  <hr className="n2" /></div><br /><br />
                    <div className="chart1">
                        <LineChart data={lineData} />
                    </div> 
                    <br /><br /><div className="name">  <hr /></div>
                    <div className="chart2">
                        <AreaChart data={areaData} />
                    </div>
                </div>
                <div className="graph">
                <div className="name2"> </div><br />
                    <div className="evaluation1">
                        <div className="evaluation">
                            <PieGraph data={evalData} />
                            <HorizontalBarChart data={evalData} />
                        </div>
                    </div>
                    <div className="name3"> </div><br />
                    <div className="evaluation2">
                        <div className="evaluation">
                            <PieGraph data={evalData2} />
                            <HorizontalBarChart data={evalData2} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>);
}
    

export default SideBarHome