import React, {useState} from "react"
import '../../css/evaluation.css';
import EvaluationLineChart from "sidebar/component/evaluation/line_graph";
import EvaluationStickGraph from "sidebar/component/evaluation/stcik_graph";
import EvaluationHistory from "./evaluation_history";

const EvaluationTable = ({lineData, lineData2, evalData, evalData2}) => {

    //  
    const [isHistory, setIsHistory] = useState(false);
    const [historyText, setHistoryText] = useState('');
    
    //    
    const handleEditClick = () => {
        setIsHistory(true);
        setHistoryText('OCR');
    };

    const handleEditClick2 = () => {
        setIsHistory(true);
        setHistoryText('recommend');
    };

    //    
    const handleEditClickFalse = () => {
        setIsHistory(false);
    };

    return (
        <>
            {isHistory ? (
                <div>
                    <EvaluationHistory historyText={historyText} handleHistory={handleEditClickFalse}/>
                </div>
            ) : (
            <div className="e_total">
                    <div className='titleBox'>
                    <div className="title">() </div>
                    <div className="title2">Evaluation Manage for admin</div><hr className="n1"/><br /><br /><br />
                    </div>
                <div className="evaluation-container">
                   
                    <h2 className="Egraph-text">OCR</h2>
                    <div className="V_H_Box">
                    <button onClick={handleEditClick} className="history">History</button>
                    <div className="versionText">  V1.0.0</div>
                    </div>
                    <div className="graphRow">
                        <div style={{marginRight:"70px"}}>
                            <EvaluationLineChart data={lineData} />
                            <div className="E_D_O">   </div>
                        </div>
                        <div style={{marginLeft:"70px"}}>
                            <EvaluationStickGraph data={evalData} />
                            <div className="E_D_O">   </div>
                        </div>
                        
                    </div>'
                    
                    <h2 className="Egraph-text2"> </h2>
                    <div className="V_H_Box">
                    <button onClick={handleEditClick} className="history">History</button>
                    <div className="versionText">  V1.0.0</div>
                    </div>
                    <div className="graphRow">
                        <div style={{marginRight:"70px"}}>
                            <EvaluationLineChart data={lineData2} />
                            <div className="E_D_O">   </div>
                        </div>
                        <div style={{marginLeft:"70px"}}>
                            <EvaluationStickGraph data={evalData2} />
                            <div className="E_D_O">   </div>
                        </div>
                    </div>
                </div>
            </div>
            )}
        </>
    );
}

export default EvaluationTable