import React, { useState } from "react";
import IngredientInput from "sidebar/component/ingredient/ingredient_input";
import IngredientRadio from "sidebar/component/ingredient/ingredient_radio";
import '../../css/ingredient.css'
import IngredientInput2 from "sidebar/component/ingredient/ingredient_input2";
import Swal from "sweetalert2";

const IngredientInfo = ({ label, rowData, goBack, status, setStatus }) => {
    
    const [data, setData] = useState(rowData);
    const [skinTypesFeaturesData, setSkinTypesFeaturesData] = useState(data.skinTypeFeatures);
    
    //  
    const handleSkinEffectChange = (index, fieldName, newValue) => {
        const newskinTypeEffect = [...skinTypesFeaturesData];
        newskinTypeEffect[index][fieldName] = newValue;
        setSkinTypesFeaturesData(newskinTypeEffect);
    };

    const handleSkinEffectsChange = (index, fieldName, newValue) => {
        const newSkinTypesData = [...skinTypesFeaturesData];
        newSkinTypesData[index][fieldName] = TFTextChangeR(newValue);
        setSkinTypesFeaturesData(newSkinTypesData);
    };

    const handleSkinTypesChange = (index, fieldName, newValue) => {
        const newskinTypeEffect = [...skinTypesFeaturesData];
        newskinTypeEffect[index][fieldName] = skinTypeCTextChangeR(newValue);
        setSkinTypesFeaturesData(newskinTypeEffect);
    };

    //  
    const handleNameChange = (val) => {
        setData(prevData => ({
            ...prevData,
            name: val
        }));
    };

    //  
    const handleDangerChange = (val) => {
        setData(prevData => ({
            ...prevData,
            danger_status: dangerTextChangeR(val)
        }));
    };
    
    //  
    const handlePurposeChange = (value) => {
        const newPurposes = value.split(',').map((purpose, index) => {
            const existingPurpose = data.purposes[index];
            const newPurpose = {
                purpose: purpose.trim()
            };
            if (existingPurpose && existingPurpose.id !== undefined) {
                newPurpose.id = existingPurpose.id;
            } else {
                delete newPurpose.id; // id      
            }
            return newPurpose;
        });
        setData(prevData => ({
            ...prevData,
            purposes: newPurposes
        }));
    };

    


    //  
    const handleFeaturesChange = (value) => {
        const newFeatures = value.split(',').map((feature, index) => {
            const existingFeature = data.features[index];
            const newFeature = {
                feature: feature.trim()
            };
            if (existingFeature && existingFeature.id !== undefined) {
                newFeature.id = existingFeature.id;
            }
            return newFeature;
        });
        setData(prevData => ({
            ...prevData,
            features: newFeatures
        }));
    };
    
    

    //   
    const handleADangerChange = (val) => {
        setData(prevData => ({
            ...prevData,
            allergy_status: dangerTextChangeR(val)
        }));
    };

    //  
    const handleGradeChange = (val) => {
        setData(prevData => ({
            ...prevData,
            grade: val
        }));
    };
    
    const dangerData = [
        { name: "O" },
        { name: "X" },
    ];

    const gradeData = [
        { name: "1" },
        { name: "2" },
        { name: "3" },
        { name: "4" },
        { name: "5" },
        { name: "6" },
        { name: "7" },
        { name: "8" },
        { name: "9" },
    ];

    const skinTypeData = [
        { name: "" },
        { name: "" },
        { name: "" },
    ];

    const TFData = [
        { name: "" },
        { name: "" },
    ];

    const skinTypeCTextChange = (val) => {
        if(val === 'DRY'){
            return ''
        }else if(val === 'OILY'){
            return ''
        }else{
            return ''
        }
    }

    const skinTypeCTextChangeR = (val) => {
        if(val === ''){
            return 'DRY'
        }else if(val === ''){
            return 'OILY'
        }else{
            return 'SENSITIVE'
        }
    }

    const TFTextChange = (val) => {
        if(val){
            return ''
        }else{
            return ''
        }
    }

    const TFTextChangeR = (val) => {
        if(val == ''){
            return true
        }else{
            return false
        }
    }

    const dangerTextChange = (val) => {
        if(val){
            return 'O'
        }else{
            return 'X'
        }
    }

    const dangerTextChangeR = (val) => {
        if(val == 'O'){
            return true
        }else{
            return false
        }
    }

    const handleRevise = () => {

        

        

        const url = label == '' ? `http://ceprj.gachon.ac.kr:60006/api/admin/ingredient/update/${data.id}` : 'http://ceprj.gachon.ac.kr:60006/api/admin/ingredient/add'
        if(label == ''){
            delete data.id;
        }
        console.log(data);
        if(label == ''){
            Swal.fire({
                title: '  ?',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: '',
                cancelButtonText: '',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
            }).then((res) => {
                if (res.isConfirmed) {
                    fetch(url, {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify(data),
                    })
                        .then(response => {
                            Swal.fire({
                                title: "  .",
                                icon: "success"
                              });
                            
                                console.log('');
                                console.log(data);
                                goBack();
                                setStatus(1);
                            
                        })
                        .catch(error => {
                            console.error('Error:', error);
                        });
                }
            });

        }
        else{

            Swal.fire({
                title: ' ?',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: '',
                cancelButtonText: '',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
            }).then((res) => {
                if (res.isConfirmed) {
                    fetch(url, {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify(data),
                    })
                        .then(response => {
                           
                                Swal.fire({
                                    title: "  .",
                                    icon: "success"
                                  });
                                console.log('');
                                console.log(data);
                                goBack();
                                setStatus(1);
                            
                        })
                        .catch(error => {
                            console.error('Error:', error);
                        });
                }
            });

        }
        
    }

    const handleDelete = () => {



        Swal.fire({
            title: '  ?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            }).then((res) => {
              if (res.isConfirmed) {
                fetch('http://ceprj.gachon.ac.kr:60006/api/admin/ingredient/delete/' + data.id, {
            method: 'DELETE',
            headers: {
              'Content-Type': 'application/json',
            },
          })
            .then(response => {
              if (response.ok) {
                Swal.fire({
                  title: " !",
                  icon: "success"
                });
                goBack();
                setStatus(1);
                console.log('');
              } else {
                Swal.fire({
                    title: " ",
                    icon: "error"
                  });
                throw new Error(' .');
              }
            })
            .catch(error => {
                Swal.fire({
                    title: " ",
                    icon: "error"
                  });
              console.error('Error:', error);
            });
                  }
            });
    }

    const handleAddSkinTypeFeatures = () => {
        setSkinTypesFeaturesData([...skinTypesFeaturesData, { skin_type: 'DRY', skinDescription: '', positivity_status: false }])
        setData(prevData => ({
            ...prevData,
            skinTypeFeatures: skinTypesFeaturesData
        }));
    };
    // skinTypesData skinTypesEffectData skinTypeEffectData
    const handleRemoveSkinTypeFeatures = () => {
        const skinTypeFeatureData = [...skinTypesFeaturesData];
        skinTypeFeatureData.splice(skinTypesFeaturesData.length-1, 1); 
        console.log(skinTypeFeatureData);
        setData(prevData => ({
            ...prevData,
            skinTypeFeatures: skinTypeFeatureData
        }));
    };

    return (
    <div className="ingredient_Total_Box">
        <div className='infotext'> {label}</div>
        <div className="ingredientInfo-container">
            <div className="ingredientText-container">
                <IngredientInput label=' ' onChange={handleNameChange} value={data.name}/>
                <IngredientInput label=' ' onChange={handlePurposeChange} value={data.purposes.map(item => item.purpose).join(', ')}/>
                <IngredientInput label=' ' onChange={handleFeaturesChange} value={data.features.map(item => item.feature).join(', ')}/>
                <div>
                    <span style={{fontSize:"16px", fontWeight:"600"}}>  </span>
                    {/* <div className="ingredient_radio_Box"> */}
                    <div >
                    {data.skinTypeFeatures.map((item, index) => (
                        <div style={{marginBottom:"50px", borderBottom:"1px solid grey"}}>
                            <IngredientRadio text=' ' data={skinTypeData} selectedValue={skinTypeCTextChange(item.skin_type)} handleChange={(newValue) => handleSkinTypesChange(index, 'skin_type', newValue)}/>
                            <IngredientRadio text='' data={TFData} selectedValue={TFTextChange(item.positivity_status)} handleChange={(newValue) => handleSkinEffectsChange(index, 'positivity_status', newValue)}/>
                            <IngredientInput2 label=' ' onChange={(newValue) => handleSkinEffectChange(index, 'skinDescription', newValue)} value={item.skinDescription}/>
                        </div>                        
                    ))}
                        <button className='dataAddBts' onClick={handleAddSkinTypeFeatures}></button>
                        <button className='dataAddBts' onClick={handleRemoveSkinTypeFeatures}></button>
                    </div>
                </div>
            </div>
            <div className="ingredient_radio_Box2">
                <IngredientRadio text=' ' data={dangerData} selectedValue={dangerTextChange(data.danger_status)} handleChange={handleDangerChange}/>
                <IngredientRadio text= '  '  data={dangerData} selectedValue={dangerTextChange(data.allergy_status)} handleChange={handleADangerChange}/>
                <IngredientRadio text=' ' data={gradeData} selectedValue={String(data.grade)} handleChange={handleGradeChange}/>
                <div className='bts'>
                    <button onClick={handleRevise} className='bt' >{label}</button>
                    {label == '' ? (<button onClick={handleDelete} className='bt' ></button>) : (<></>)}
                    <button className='bt' onClick={goBack}></button>
                </div>
            </div>
        </div>
        </div>
    );
};

export default IngredientInfo;
